import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    isOnline: true,
    connect_wifiname:'',
  },
  mutations: {},
  actions: {
    update_online_status({ commit, state }, v) {// 更新网络状态
      state.isOnline = v;
    },
    update_connect_wifiname({ commit, state }, v) {// 更新网络名称
      state.connect_wifiname = v;
    },
  }
});



