export default class WifiManager {
  constructor() {
    this.select_index = 0;
    this.wifi_list = [];
    this.wireless = null;
    if (process.env.NODE_ENV === "production") {
      this.wifi = window["wifi"];
      this.init();
    }
  }
  init() {
    this.wifi.init({
      iface: null // network interface, choose a random wifi interface if set to null
    });
  }

  get_wifi_list() {
    return new Promise((resolve, reject) => {
      try {
        this.wifi.scan((err, networks) => {
          if (err) {
            console.error(err);
            return reject(err);
          } else {
            console.log(networks);
            return resolve(networks);
          }
        });
      } catch (error) {
        reject(error);
        console.error(error);
      }
    });
  }
  connect(name, pwd) {
    return new Promise((resolve, reject) => {
      try {
        this.wifi.connect({ ssid: name, password: pwd }, err => {
          try {
            if (err) {
              console.error(err);
              return reject(err);
            }
            console.log("Connected");
            return resolve(true);
          } catch (error) {
            return reject(error);
          }
        });
      } catch (error) {
        console.error(error);
        reject(error);
      }
    });
  }
  disconnect(name, pwd) {
    return new Promise((resolve, reject) => {
      try {
        this.wifi.disconnect(err => {
          if (err) {
            console.error(err);
            return reject(err);
          }
          console.log("Connected");
          return resolve(true);
        });
      } catch (error) {
        return reject(error);
      }
    });
  }
  getCurrentConnections() {
    return new Promise((resolve, reject) => {
      try {
        this.wifi.getCurrentConnections((err, currentConnections) => {
          if (err) {
            reject(err);
            console.error(err);
          }
          if (currentConnections && currentConnections.length > 0) {
            resolve(currentConnections[0]);
          }else{
              reject();
          }
          console.log(currentConnections); 
        });
      } catch (err) {
        reject(err);
        console.error(err);
      }
    });
  }
}



