export default class NetworkManager {
  constructor(app) {
    this.isOnline = navigator && navigator.onLine;
    this.$app = app;
    this.$app.$store.dispatch('update_online_status',this.isOnline);
  }

  init() {
    window.addEventListener("online", ()=>{
        this.$app.$store.dispatch('update_online_status',true);
    });
    window.addEventListener("offline", ()=>{
        this.$app.$store.dispatch('update_online_status',false);
    });
  }
}



