import Vue from "vue";
import { Button, Dialog, Input, Select, Option } from "element-ui";
import LightController from "../services/LightController.js";
import WifiManager from "../services/WifiManager";
import VueTouchKeyboard from "vue-touch-keyboard";
import style from "vue-touch-keyboard/dist/vue-touch-keyboard.css";
import mixins from "./_yiqi.mixins";

Vue.use(VueTouchKeyboard);
Vue.component(Button.name, Button);
Vue.component(Dialog.name, Dialog);
Vue.component(Input.name, Input);
Vue.component(Select.name, Select);
Vue.component(Option.name, Option);

function nums(n) {
  return n > 9 ? n + "" : "0" + n;
}
const lc = new LightController();
export default {
  mixins: [mixins],
  data() {
    return {
      isstart: false,
      dialog: {
        visible: false
      },
      dialog_wifi: {
        visible: false,
        pwd: "",
        name: "",
        selected: -1,
        list: [],
        result: "",
        connected: null
      },
      dialog_net: {
        visible: false
      },
      timer: {
        tid: null,
        txt: "",
        btime: 10 * 60,
        time: 10 * 60 // 分钟
      }
    };
  },
  computed: {
    t_txt() {
      let t = this.timer.time;
      return nums(parseInt(t / 60)) + ":" + nums(t % 60);
    },
    time_over() {
      return this.timer.tid === null && this.timer.time === 0;
    },
    online_status() {
      return this.$store.state.isOnline;
    },
    connect_wifi_name(){
        if(this.online_status && this.dialog_wifi.connected && this.dialog_wifi.connected.ssid){
            return this.dialog_wifi.connected.ssid
        }
        return undefined;
    }
  },
  watch: {
    time_over() {},
    online_status(n, o) {
      console.log("网络状态", n); 

      this.dialog_wifi.connected = null;
      this.update_connect(); 
      if(n){
          this.dialog_wifi.pwd = '';
      }
    }
  },
  mounted() {
    console.log("online_status", this.online_status);
    this.wifim = new WifiManager();
    this.update_connect();
  },
  methods: {
    hide_dialog() {
      this.dialog.visible = false;
      setTimeout(() => {
        this.isstart = true;
        this.timer.time = this.timer.btime;
        this.startTimer();
      }, 300);
    },
    start_btn() {
      if (!this.online_status) {
        this.dialog_net.visible = true;
        return;
      }
      if (!this.isstart) {
        this.dialog.visible = true;
      } else {
        this.dialog.visible = false;
        this.isstart = false;
        lc.stop();
        this.stopTimer();
      }
    },
    stopTimer() {
      this.timer.tid && clearInterval(this.timer.tid);
      this.timer.tid = null;
    },
    startTimer() {
      if (this.timer.tid) return;
      this.timer.tid = setInterval(() => {
        let t = (this.timer.time = this.timer.time - 1);
        this.timer.txt = nums(parseInt(t / 60)) + ":" + nums(t % 60);
        if (t == 0 && this.timer.tid) {
          this.stopTimer();
        }
      }, 1000);
      this.timer.txt =
        nums(parseInt(this.timer.time / 60)) + ":" + nums(this.timer.time % 60);
    },

    async openDialog() {
      console.log("openDialog");
      this.dialog_wifi.pwd = "";
      this.status.list_loading = true;
      
      if (process.env.NODE_ENV == "production") {
        try {
          this.update_connect();
          this.dialog_wifi.list = await this.wifim.get_wifi_list();
          this.status.list_loading = false;
        } catch (error) {
          console.error(error);
        }
      } else {
        this.status.list_loading = false;
        this.dialog_wifi.list = [
          {
            mac: "ec:26:ca:3a:67:76",
            ssid: "m3lean_wlanA_5G",
            signal_level: "-87",
            security: "WPA(PSK/AES/AES) WPA2(PSK/AES/AES)"
          },
          {
            mac: "00:90:4c:88:88:89",
            ssid: "YL1001_APP",
            frequency: "2417",
            signal_level: "-83",
            security: "WPA(PSK/AES,TKIP/TKIP) WPA2(PSK/AES,TKIP/TKIP)"
          },
          {
            mac: "88:25:93:5c:45:a7",
            ssid: "sysweal_2.4G",
            frequency: "2412",
            signal_level: "-42",
            security: "WPA(PSK/AES/AES) WPA2(PSK/AES/AES)"
          },
          {
            mac: "d4:ee:07:29:6d:70",
            ssid: "elanw01",
            frequency: "2447",
            signal_level: "-89",
            security: "WPA(PSK/AES/AES) WPA2(PSK/AES/AES)"
          }
        ];
      }
    },
    async connect() {
      this.accept();
      let name = "";
      if (this.dialog_wifi.selected > -1) {
        name = this.dialog_wifi.list[this.dialog_wifi.selected].ssid;
      }
      if (!name && this.dialog_wifi.connected) {
        name = this.dialog_wifi.connected.ssid;
      }
      console.log("...", name, this.dialog_wifi.pwd);
      this.dialog_wifi.result = "";
      if (!this.dialog_wifi.pwd || !name) {
        return;
      } 
      this.status.connect_loading = true;
      try {
        let result = await this.wifim.connect(name, this.dialog_wifi.pwd);
        console.log("result ....", result);
      } catch (error) {
        console.error(error);
      }
      const top_tt = id => {
        clearInterval(id);
        this.dialog_wifi.selected = -1;
        this.status.connect_loading = false;
      };
      let i = 0;

      var ttid = setInterval(() => {
        console.table(this.dialog_wifi);
        if (this.online_status) {
          
          this.dialog_wifi.result = "连接成功";
          top_tt(ttid);
        }
        i++;
        if (i > 20) {
          this.dialog_wifi.result = "连接失败";
          top_tt(ttid);
        }
      }, 1000);
    }
  }
};



