export default {
  data() {
    return {
      status: {
        list_loading: false,
        connect_loading: false,
        connect_waiting: false
      },
      keyboard: {
        visible: false,
        input: null
      }
    };
  },
  methods: {
    show(e) {
      this.keyboard.input = e.target;
      if (!this.keyboard.visible) this.keyboard.visible = true;
    },

    hide() {
      this.keyboard.visible = false;
    },
    change(v) {
      this.dialog_wifi.pwd = (v + "").trim();
    },
    accept(text) {
      this.hide();
    },
    update_connect() {
      this.wifim.getCurrentConnections().then(
        res => {
          this.$store.dispatch("update_connect_wifiname", res);
          this.dialog_wifi.connected = res;
        },
        error => {
          console.error(error);
        }
      );
    }
  }
};



